import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import BeforeStart from '../../components/BeforeStart'
import { responsive } from '../../components/ThemeProvider/theme'

const OptOne = () => {
  return (
    <BeforeStart
      background={responsive('linear-gradient(90deg, #CABFEC 0%, transparent 100%)', 'linear-gradient(#CABFEC 0%, transparent 100%)')}
      noCircle
    >
      <Box flex={1}>
        <Text fontSize={responsive('1em', '2em')}>全球熱錢支撐債市，該如何布局下一步呢</Text>
        <Text.Bold mt="2rem" fontSize={responsive('1.25em', '2.25em')}>唯有以主動、彈性以及敏捷的方式，<br />才能在後疫情時代中追求收益</Text.Bold>
      </Box>
    </BeforeStart>
  )
}

export default OptOne
