import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import BeforeStart from '../../components/BeforeStart'
import { responsive } from '../../components/ThemeProvider/theme'

const ChangeOne = () => {
  return (
    <BeforeStart
      background={responsive("linear-gradient(90deg, #82CAD0 0%, transparent 100%)", "linear-gradient(#82CAD0 0%, transparent 100%)")}
      noCircle
    >
      <Box flex={1}>
        <Text.Bold fontSize={responsive('1.5em', '2.25em')}>誰是下一個「顛覆性產業」？</Text.Bold>
        <Text mt="2rem" fontSize={responsive('1em', '1.5em')}>
          科技突破與變革的加速，將反映在投資機會上， 若無系統性聚焦分析，不易理解其創新模式，於此施羅德與投資人一同掌握此主題。
        </Text>
      </Box>
    </BeforeStart>
  )
}

export default ChangeOne
