import React from 'react'

import Box from '../Box'
import { responsive } from '../ThemeProvider/theme'

import weatherbg from './weatherbg.png'

const CircleBgModule = ({ children, extend }) => {
  return (
    <Box position="relative" height="100%" overflow="hidden">
      <Box
        position="absolute"
        height="100%"
        backgroundImage={`url(${weatherbg})`}
        backgroundPosition="50% 50%"
        backgroundSize="cover"
        backgroundRepeat="no-repeat"
        opacity="0.35"
        left={extend}
        right={extend}
      />
      <Box.AbsCenter width="100%">
        {children}
      </Box.AbsCenter>
    </Box>
  )
}

export default CircleBgModule
