import React from 'react'

import backgroundBg from './background.png'
import Box from '../Box'
import Flex from '../Flex'
import BackgroundImage from '../BackgroundImage'
import { responsive } from '../ThemeProvider/theme'

const BeforeStart = ({ background, noCircle, children }) => {
  return (
    <Box position="relative" color="white" height="100vh" overflow="hidden">
      {noCircle && (
        <BackgroundImage
          src={backgroundBg}
          width={responsive('300%', 'auto')}
          opacity="0.35"
          backgroundPosition={responsive('150% 50%', '50% 50%')}
        />
      )}
      <Flex
        flexDirection={responsive('column', 'row')}
        position="absolute"
        left="50%"
        top="50%"
        transform="translate(-50%, -50%)"
        width={responsive('80%', 'auto')}
      >
        <Box
          background={background}
          height={responsive('2em', 'auto')}
          width={responsive('50%', '2.5em')}
          mr="2.5em"
          mb={responsive('1em', 0)}
          opacity="0.5"
        />
        {children}
      </Flex>
    </Box>
  )
}

export default BeforeStart
