import React from 'react'
import { groupBy, map, mapValues, size } from 'lodash'
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverBody,
  Stack,
  AspectRatio,
} from "@chakra-ui/react"

import { responsive } from '../../../components/ThemeProvider/theme'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import Box from '../../../components/Box'
import Button from '../../../components/Button'
import Image from '../../../components/Image'
import CircleBgModule from '../../../components/CircleBgModule'
import Container from '../../../components/Container'

import high from './high.svg'
import med from './med.svg'
import low from './low.svg'

import data from './data.json'
import links from '../links'

const grouped = mapValues(groupBy(data, 'type'), list => groupBy(list, 'field'))

const gradientBase = '#002a5e'

const order = [
  { key: '低', note: '持續升高中', bg: '#82cad0', icon: low },
  { key: '中', bg: '#b3d476', icon: med },
  { key: '高', bg: '#fcde3d', icon: high },
]

const ChangeFields = () => {
  return (
    <CircleBgModule>
      <Container>
        <Box ml={responsive(0, '4em')}>
          <Text color="white" fontWeight="700" mb="0.75em" letterSpacing="0.2em" fontSize={responsive('0.875em', '2em')}>科技突破與變革的加速，將反映在投資機會上</Text>
          <Flex mx={responsive('0.5em', '-0.5em')} flexDirection={responsive('column', 'row')}>
            {order.map(d => {
              const fieldCount = size(grouped[d.key])
              return (
                <Box width="100%" mx="0.5em" my="0.25em" key={d.key}>
                  <Box bgImage={responsive(`linear-gradient(to right, ${d.bg}, ${gradientBase})`, `linear-gradient(${d.bg}, ${gradientBase})`)} p={responsive('0.25em', '1em')} rounded="2em" height="100%">
                    <Flex my={responsive('0.5em', '1em')} alignItems="center" pl={responsive('1em', 0)}>
                      <Image src={d.icon} width={responsive('2em', '4em')} />
                      <Flex flexDirection={responsive('row', 'column')} color="white" pl="1em" alignItems={responsive('flex-end', 'flex-start')}>
                        <Text fontSize={responsive('1em', '2.5em')} fontWeight="500" letterSpacing="0.125em">顛覆性{d.key}</Text>
                        {d.note
                          ? <Text ml={responsive('0.5em', 0)} mt="-0.5em" fontSize={responsive('0.875em', '1.25em')} letterSpacing="0.125em">{d.note}</Text>
                          : <Box display={responsive('none', 'block')} width="100%" borderBottom="2px solid" />}
                      </Flex>
                    </Flex>
                    <Flex flexWrap="wrap">
                      {map(grouped[d.key], (keywords, field) => (
                        <Box key={field} width={fieldCount > 4 ? responsive(1 / 5, 1 / 4) : responsive(1 / 4, 1 / 2)} p={responsive('0.25em', '0.375em')}>
                          <Box.Relative>
                            <Popover
                              trigger="hover"
                              placement="bottom"
                              gutter="0"
                              size="100%"
                            >
                              <PopoverTrigger>
                                <AspectRatio ratio={responsive(1.35, 1)}>
                                  <Box
                                    borderRadius="1em"
                                    bg={d.bg}
                                    p="0.5em"
                                    color="primary"
                                    transition="all 0.5s"
                                    _hover={{
                                      background: "primary",
                                      color: "white",
                                      borderRadius: '1em 1em 0 0',
                                    }}
                                  >
                                    <Text lineHeight="1.25" fontSize={responsive('0.875em', '1.125em')} fontWeight="700" textAlign="center" whiteSpace="pre-wrap">
                                      {field}
                                    </Text>
                                  </Box>
                                </AspectRatio>
                              </PopoverTrigger>
                              <PopoverContent bg={d.bg} color="primary" border="none" borderRadius="0 0 1em 1em" boxShadow="2px 2px 4px rgba(0,0,0,0.3)">
                                <PopoverBody>
                                  <Stack px="5%" pb="1em">
                                    {keywords.map((kw, i) => (
                                      <Box
                                        key={i}
                                        textAlign="center"
                                        py="0.25em"
                                        fontSize="0.875em"
                                        fontWeight="700"
                                        borderBottom="1px solid white"
                                      >{kw.keyword}</Box>
                                    ))}
                                  </Stack>
                                </PopoverBody>
                              </PopoverContent>
                            </Popover>
                          </Box.Relative>
                        </Box>
                      ))}
                    </Flex>
                  </Box>
                </Box>
              )
            })}
          </Flex>
          <Flex my="1em">
            <Box flex="1" borderTop="1px solid" color="white" py="0.5em" fontSize={responsive('0.75em', '1em')}>
              資料來源 : 施羅德投資，2020年11月3號。
            </Box>
            <Box ml="1em">
              <Button
                href={links[3]}
                rounded="full"
                py="0.75em"
                backgroundImage="linear-gradient(270deg, #82CAD0 -10.9%, #002962 74.32%)"
                className="link-track"
                onClick={() => {
                  window.gaTrackClick(`Link: ${links[3]}`)
                }}
              >完整論壇回顧</Button>
            </Box>
          </Flex>
        </Box>
      </Container>
    </CircleBgModule>
  )
}

export default ChangeFields
