import React, { useEffect, useRef } from 'react'
import styled from '@emotion/styled'

import { ReactComponent as Chart } from './charts/chart-1-1.svg'

const StyledChart = styled(Chart)`
.line {
  stroke-dasharray: 960px;
  stroke-dashoffset: 960px;
  transition: stroke-dashoffset 1.5s ease;
}
`

const Chart11 = () => {
  const chartRef = useRef()
  useEffect(() => {
    setTimeout(() => {
      if (chartRef.current) {
        chartRef.current.querySelectorAll('.line').forEach(ele => {
          ele.style.strokeDashoffset = '0px'
        })
      }
    }, 1000)
  }, [])
  return (
    <StyledChart ref={chartRef} />
  )
}

export default Chart11
