import React, { useEffect, useRef, useState } from 'react'
import styled from '@emotion/styled'
import { get } from 'lodash'

import ArrowIcon from '../../../components/ArrowIcon'
import Box from '../../../components/Box'
import theme, { responsive } from '../../../components/ThemeProvider/theme'
import { ReactComponent } from './side-nav.svg'

import names from '../names'

const SVG = styled(ReactComponent)`
text {
  pointer-events: none;
}
.nav {
  cursor: pointer;
}
`

let svgParts

const SideNav = ({ bg, active, activePart, onClick, colors }) => {
  const [isOpen, setIsOpen] = useState()
  const svgRef = useRef()
  useEffect(() => {
    svgParts = svgRef.current.querySelectorAll('.nav')
    svgParts.forEach((nav, i) => {
      nav.style.WebkitTransition = 'all 0.5s';
      nav.style.MozTransition = 'all 0.5s';
      nav.style.transition = 'all 0.5s';
      let lock
      nav.onclick = () => {
        window.gaTrackClick(`SideNav-${names[i]}`)
        onClick(i)
        lock = true
        prevFill = null
        setTimeout(() => {
          lock = false
        }, 500)
      }
      let prevFill
      nav.addEventListener('mouseenter', e => {
        if (!lock) {
          if (!prevFill) prevFill = nav.getAttribute('fill')
          nav.setAttribute('fill', get(theme, ['colors', colors[i]]))
        }
      });

      nav.addEventListener('mouseleave', e => {
        if (!lock) {
          nav.setAttribute('fill', prevFill)
          prevFill = null
        }
      });
    })
  }, [])
  useEffect(() => {
    svgParts.forEach(part => {
      part.setAttribute('fill', 'white')
      part.setAttribute('opacity', '0.7')
    })
    if (svgParts[activePart]) {
      svgParts[activePart].setAttribute('fill', get(theme, ['colors', bg]))
      svgParts[activePart].setAttribute('opacity', 1)
    }
  }, [activePart])
  return (
    <>
      {isOpen && <Box.Fixed top="0" left="0" right="0" bottom="0" onClick={() => setIsOpen(false)} />}
      <Box.Fixed
        width={responsive('20em', '25em', '30em')}
        bg={isOpen ? 'primary' : bg}
        rounded="full"
        left="0"
        top={responsive('30%', '50%')}
        transform={`translate(-${isOpen ? 50 : (active ? 90 : 100)}%, -50%)`}
        transition="all 0.5s ease"
        onClick={() => setIsOpen(!isOpen)}
      >
        <SVG ref={svgRef} />
        <Box.Absolute top="50%" right="0.75em" transform="translateY(-50%)">
          <ArrowIcon
            color="white"
            fontSize={responsive('1em', '1.25em', '1.375em')}
            transition="all 0.5s ease"
            transform={`rotate(${isOpen ? '180deg' : 0})`}
          />
        </Box.Absolute>
      </Box.Fixed>
    </>
  )
}

export default SideNav
