import React, { useEffect } from 'react'

import Title from '../../../components/Title'
import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
// import Image from '../../../components/Image'
import BackgroundImage from '../../../components/BackgroundImage'
import Container from '../../../components/Container'
import { responsive } from '../../../components/ThemeProvider/theme'

import background from './background.png'

const predictData = [
  [
    '看好股票',
    '相較於歐洲更看好新興市場',
    '相較於S&P更看好Russell2000',
    '相較於A50指數更看好MSCI中國指數',
    '看好高收益債',
  ],
  [
    '看好中國政府公債',
    '看好新興市場本地債',
    '相較於美國10年期公債更看好澳洲10年期公債',
    '相較於新加坡幣更看好日圓',
    '相較於澳幣/加拿大幣更看好美元',
  ]
]

const PredictTwo = ({ getFpApi }) => {
  return (
    <Flex flexDirection="column" >
      <Title title="展望2021年，我們預期" />
      <Box flex={1} position="relative">
        <Box position="absolute" opacity="0.2" top="0" bottom="0" left="0" right="0">
          <BackgroundImage src={background} />
        </Box>
        <Container
          as={Flex}
          py={responsive('2em', null, '2.5em', null, '5em')}
          alignItems="center"
          justifyContent="space-around"
          flexDirection={responsive('column', 'row')}
          position="relative"
        >
          {predictData.map((d, i) => (
            <Box key={i} width={responsive('100%', 'auto')} pt={i && responsive('1em', 0)}>
              {d.map((text, k) => (
                <Flex pt={k && responsive('1em', '1.75em')} alignItems="center" key={k}>
                  <Box color="yellow" letterSpacing="0.25em" fontSize={responsive('1em', '2em')}>
                    {String(k + i + d.length).length < 2 && 0}{i ? k + i + d.length : k + 1}
                  </Box>
                  <Box mx="1.25em" height="2em">
                    <Box height="100%" width="2px" bg="#8195B0" transform="rotate(25deg)" />
                  </Box>
                  <Box
                    role="button"
                    flex={1}
                    px="1.875rem"
                    py="0.5em"
                    color="white"
                    rounded="full"
                    textAlign="left"
                    background="linear-gradient(90deg, #053D72 0%, #F5A737 100%)"
                    fontSize={responsive(text.length > 20 ? '0.875em' : '1em', '1.25em')}
                    onClick={() => {
                      const id = (i * 5) + k
                      if (id < 3) {
                        getFpApi().moveTo(5 + id)
                      } else {
                        getFpApi().moveTo(5 + 2, id - 2)
                      }
                    }}
                  >
                    {text}
                  </Box>
                </Flex>
              ))}
            </Box>
          ))}
        </Container>
      </Box>
    </Flex>
  )
}

export default PredictTwo
