import React from 'react'

import Title from '../../../components/Title'
import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Container from '../../../components/Container'
import { responsive } from '../../../components/ThemeProvider/theme'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'

import chart from './chart_1.svg'

const PredictOne = () => {
  const { isMobile } = useResponsive()
  return (
    <Flex flexDirection="column" bg="white">
      <Title titleWidth={responsive('60%', 'auto')} title="施羅德 2021年十大預測" subTitle={!isMobile && "以前瞻視野，助您建構未來投資策略"} />
      <Container as={Flex} flex={1} py={responsive('3em', null, '1.5em', null, '3em')} alignItems="center" flexDirection={responsive('column', 'row')}>
        <Box color="prussianBlue" mr={responsive(0, '1em')} px={responsive('2em', 0)} flex={1}>
          <Text fontWeight="bold" fontSize={responsive('1.125em', null, '1.25em', '1.625em', '2em')}>從流動性驅動走向經濟復甦<br />，現在正是時候思考</Text>
          <Text fontWeight="bold" my="1em" fontSize={responsive('1.25em', null, '1.5em', '1.75em', '2.75em')}>我們該如何進行下一步投資?</Text>
          <Text color="brightBlue" fontSize={responsive('1em', null, null, '1.125em', '1.375em')}>在疫情趨使下，各國政府施行大規模的貨幣與財政支持政策。現在我們已步入復甦的階段，我們預期2021年底GDP水準將會回歸常態。</Text>
        </Box>
        <Box width={responsive('100%', '55%')} pt={responsive('1em', 0)}>
          <Box color="prussianBlue">全球經濟成長貢獻來源</Box>
          <Box py="0.5em" px={responsive(0, null, '7.5%', null, 0)}>
            <Image src={chart} />
          </Box>
          <Box borderTop="1px solid" pt="0.5em" fontSize={responsive('0.875em', '1em')}>資料來源: 施羅德經濟研究團隊，資料截至2020/9/21。</Box>
        </Box>
      </Container>
    </Flex>
  )
}

export default PredictOne
