import React, { useState } from 'react'
import { IconButton } from '@chakra-ui/react'
import { InfoIcon } from '@chakra-ui/icons'

import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Button from '../../../components/Button'
import Title from '../../../components/Title'

import number from './chart-3-1-sub.svg'
import Chart31 from './Chart31'
import { responsive } from '../../../components/ThemeProvider/theme'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'

const OptTwo = () => {
  const { isMobile } = useResponsive()
  const [openExplain, setExplain] = useState(true)
  return (
    <Flex flexDirection="column" bg="white" height="100vh">
      <Title
        subTitle="信用資產仍提供極佳的收益機會"
        text={!isMobile && "環球收息債券策略 - \n兼具收益與抵禦下檔風險的最佳投資解決方案"}
        note={!isMobile && "僅作為舉例說明，不代表任何金融商品之推介或投資建議"}
        exTitle="什麼是每單位風險的收益?"
        exText="承受每單位風險所得的收益率，數值越高則表示同樣承受一單位的風險所得的收益率越高。"
        titleWidth={responsive('75%', 'auto')}
        subTitleSpacing="0.25em"
        height="auto"
        openExplain={openExplain}
        setExplain={setExplain}
      />
      <Container flex={1} as={Flex} flexDirection="column" justifyContent="center">
        {isMobile && (
          <Text px="2em" whiteSpace="pre-wrap" fontSize={responsive('0.875em', '1.375em')}>
            {"環球收息債券策略 - \n兼具收益與抵禦下檔風險的最佳投資解決方案"}
          </Text>
        )}
        <Box pt={responsive('3em', 0)} px={responsive(0, null, '10%', null, 0)}>
          {isMobile && (
            <Text pb="1em" px="1.5em" color="red" fontSize="0.75em">*{"僅作為舉例說明，不代表任何金融商品之推介或投資建議"}</Text>
          )}
          <Chart31 />
          <Box position="relative">
            <Image src={number} />
            <Box
              position="absolute"
              top={`${(-6 / 32)*100}%`}
              left={`${(228 / 394)*100}%`}
            >
              <IconButton
                isRound
                fontSize={responsive('0.5em', '1.25em')}
                onClick={() => setExplain(!openExplain)}
                icon={<InfoIcon />}
              />
            </Box>
          </Box>
        </Box>
        <Flex mt="1em" alignItems="center" px={responsive(0, null, '10%', null, 0)} flexDirection={responsive('column-reverse', 'row')}>
          <Box mr="1em" textAlign="center" mt={responsive('0.5em', 0)}>
            <Button.SingleSharp
              target="_blank"
              href="http://www.newfinancecapital.com/zh-tw/tw/asset-management/insights/global-credit-income-2020/"
              bg="linear-gradient(90deg, #002962 25%, #CABFEC 100%)"
              _hover={{ color: 'white', textDecoration: 'none' }}
              className="link-track"
              onClick={() => {
                window.gaTrackClick('Link: http://www.newfinancecapital.com/zh-tw/tw/asset-management/insights/global-credit-income-2020/')
              }}
            >
              了解更多環球收息債券策略
            </Button.SingleSharp>
          </Box>
          <Box pt="0.25em" borderTop="1px solid" fontSize={responsive('0.875em', '1em')}>
            資料來源: 施羅德投資, Bloomberg, JP Morgan 截至2020/10/30。 *此為推算值，基於過去180天期計算。除此之外，其他波動度年化回報皆以一年期為基準。債券基金收益率為有效殖利率、到期殖利率或最差殖利率。 股票型基金採取最佳股息法，資料來源Bloomberg。 過去的績效亦不代表未來績效之保證。
          </Box>
        </Flex>
      </Container>
    </Flex>
  )
}

export default OptTwo
