import React from 'react'

import CloseIcon from './CloseIcon'

import Box from '../Box'
import Text from '../Text'
import Button from '../Button'
import { responsive } from '../ThemeProvider/theme'

const ExplainBoard = ({ top, openExplain, exTitle, exText, setExplain }) => {
  return (
    <Box
      width={responsive('60%', '22.5%')}
      position="absolute"
      bg={responsive('prussianBlue', 'white')}
      px="1.75em"
      pt="1.75em"
      pb="1.875em"
      borderRadius="0 0 0 3em"
      transform={responsive('none', "translateY(-50%)")}
      right={openExplain ? '0' : "-100%"}
      top={responsive(top || '100%', '50%')}
      transition="all 0.5s"
    >
      <Text color={responsive('white', 'prussianBlue')} fontSize={responsive('1em', '1.25em')}>{exTitle}</Text>
      <Text color={responsive('white', 'prussianBlue')} fontSize={responsive('0.75em', '1em')}>{exText}</Text>
      <Box position="absolute" left="-1em" top="-0.75em">
        <Button
          onClick={() => setExplain(false)}
          py="0"
          px="0"
          bg="transparent"
          _hover={{ bg: 'transparent'}}
        >
          <CloseIcon />
        </Button>
      </Box>
    </Box>
  )
}

export default ExplainBoard
