import React from 'react'
import { useTransition, animated, config } from 'react-spring'

import Box from '../../../components/Box'
import Pages from './Pages'

const TenForecast = ({
  index,
  slideIndex,
  duration,
  visible,
  pageDirection,
  slideDirection,
  onNext,
  onPrev,
  isLast,
  children,
}) => {
  const visibleTransitions = useTransition(visible, null, {
    from: { y: 100},
    enter: { y: 0 },
    leave: { y: -100 },
    config: { ...config.wobbly, duration },
  })
  return visibleTransitions.map(({ item, key, props }) =>
    item && (
      <animated.div
        key={key}
        style={{
          transform: props.y.interpolate(y => `translate3d(0, ${y * (pageDirection === 'down' ? 1 : -1)}%, 0)`),
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          pointerEvents: 'none',
        }}
      >
        <Box.Relative height="100%" bg="primary">
          <Pages
            pageDirection={pageDirection}
            slideDirection={slideDirection}
            index={index}
            slideIndex={slideIndex}
            duration={duration}
            isLast={isLast}
            onNext={onNext}
            onPrev={onPrev}
          />
          {isLast && children}
        </Box.Relative>
      </animated.div>
    )
  )
}

export default TenForecast
