import React, { useEffect, useContext, useState, useMemo } from 'react'
import { merge, round } from 'lodash';
import {
  VictoryChart,
  VictoryTheme,
  VictoryBar,
  VictoryAxis,
} from 'victory';

import SVG from '../../../components/SVG'
import theme from '../../../components/ThemeProvider/theme'
import { dependentAxis, mainAxis } from '../../../components/ThemeProvider/victoryTheme'

import fpContext from '../fpContext'

const data = [
  { x: '環球收息\n債券策略*', y: 3.42 },
  { x: 'iShare\n全球政府公債', y: 0.26 },
  { x: 'iShare\n全球投資級債', y: 1.51 },
  { x: 'iShare JPM $\n新興市場債', y: 4.26 },
  { x: 'iShare\n全球高收益債', y: 3.94 },
  { x: 'Invesco\n高級信貸ETF', y: 4.15 },
  { x: 'MSCI\n世界指數', y: 2.03 },
]

const Chart31 = () => {
  const { currentPage } = useContext(fpContext)
  const [active, setActive] = useState()
  useEffect(() => {
    if (currentPage === 13) {
      setTimeout(() => setActive(true), 1000)
    } else {
      setTimeout(() => setActive(false), 1000)
    }
  }, [currentPage])
  return useMemo(() => (
    <SVG viewBox="0 0 540 120">
      <text
        x="4"
        y="10"
        fontSize="8"
        fontWeight="700"
        fill={theme.colors.primary}
      >(%)</text>
      {active && (
        <VictoryChart
          width={540}
          height={120}
          theme={VictoryTheme.material}
          standalone={false}
          domainPadding={{ x: 40 }}
          padding={{
            top: 20,
            bottom: 40,
            left: 28,
            right: 10,
          }}
        >
          <VictoryAxis
            dependentAxis
            style={merge({}, dependentAxis, {
              grid: {
                stroke: theme.colors.lightGray,
              },
            })}
            tickValues={[1, 3, 5]}
          />
          <VictoryAxis
            style={mainAxis}
          />
          <VictoryBar
            data={data}
            labels={({ datum }) => round(datum.y, 2) + '%'}
            animate={{ duration: 250 }}
            style={{
              data: {
                fill: d=> d.index ? theme.colors.primary : '#6bcdd3',
              },
              labels: {
                fill: theme.colors.primary,
                fontSize: 10,
                fontWeight: 700,
              },
            }}
          />
        </VictoryChart>
      )}
    </SVG>
  ), [active])
}

export default Chart31
