import React, { useContext, useEffect, useMemo, useState } from 'react'
import {
  VictoryChart,
  // VictoryGroup,
  VictoryTheme,
  VictoryLine,
  VictoryAxis,
} from 'victory';

import SVG from '../../../components/SVG';
import theme from '../../../components/ThemeProvider/theme'
import { dependentAxis, mainAxis } from '../../../components/ThemeProvider/victoryTheme'

import fpContext from '../fpContext'
import chart22 from './chart22.json'

const colors = [
  'conifer',
  'apple',
  'jungleGreen',
  'mustard',
  'darkViolet',
  'vividViolet',
]

const offsetX = chart22.reduce((of, d, i) => [...of, i ? chart22[i - 1].name.length * 12 + 20 + of[i - 1] : 0], [])

const Chart22 = () => {
  const { currentPage } = useContext(fpContext)
  const [active, setActive] = useState()
  useEffect(() => {
    if (currentPage === 9) {
      setTimeout(() => setActive(true), 1000)
    } else {
      setTimeout(() => setActive(false), 1000)
    }
  }, [currentPage])

  return useMemo(() => (
    <SVG viewBox="0 0 540 280">
      <text
        x="0"
        y="10"
        fontSize="8"
        fontWeight="700"
        fill={theme.colors.primary}
      >(基點bbs)</text>
      {active && (
        <VictoryChart
          scale={{ x: "time" }}
          width={540}
          height={280}
          theme={VictoryTheme.material}
          standalone={false}
          padding={{
            top: 20,
            bottom: 50,
            left: 28,
            right: 14,
          }}
        >
          <VictoryAxis
            dependentAxis
            tickValues={[0, 0.1, 0.2, 0.3, 0.4]}
            style={dependentAxis}
          />
          <VictoryAxis
            style={mainAxis}
          />
          {chart22.map(({ name, ...dates }, i) => (
            <VictoryLine
              key={name}
              name={name}
              data={Object.keys(dates).map((date) => ({
                x: new Date(date),
                y: Number(dates[date]),
              }))}
              animate={{ duration: 1000 }}
              style={{
                data: {
                  stroke: theme.colors[colors[i]],
                  strokeDasharray: i > 3 ? '10px' : '0px',
                  strokeLinecap: 'round',
                },
              }}
            />
          ))}
        </VictoryChart>
      )}
      <g transform="translate(38, 260)">
        {chart22.map(({ name }, i) => (
          <g key={name} transform={`translate(${offsetX[i]}, 0)`}>
            <line x1="0" y1="0" x2="18" y2="0" strokeWidth="3" stroke={theme.colors[colors[i]]} />
            <text
              x="22"
              y="3"
              fontSize="9"
              fontWeight="700"
              fill={theme.colors.primary}
            >
              {name}
            </text>
          </g>
        ))}
      </g>
    </SVG>
  ), [active])
}

export default Chart22
