import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import ArrowNext from '../../../components/ArrowNext'
import ArrowIcon from '../../../components/ArrowIcon'
import BeforeStart from '../../../components/BeforeStart'
import CircleBgModule from '../../../components/CircleBgModule'
import { responsive } from '../../../components/ThemeProvider/theme'

const WeatherThree = ({ onNext }) => {
  return (
    <CircleBgModule>
      <BeforeStart
        background={responsive("linear-gradient(90deg, #B3D476 0%, transparent 100%)", "linear-gradient(#B3D476 0%, transparent 100%)")}
      >
        <Box position="relative">
          <Text fontSize={responsive('1em', '1.5em')}>2021年氣候變化投資三大趨勢:</Text>
          <Text fontSize={responsive('1.5em', '3em')}>電氣化、氫能、永續消費</Text>
          <Text textAlign={responsive('left', 'right')} fontSize={responsive('1em', '1.5em')}>帶來投資新風貌</Text>
          <Flex
            alignItems="center"
            position="absolute"
            left={responsive('unset', '110%')}
            right={responsive('0%', 'unset')}
            bottom={responsive('0', '-0.25em')}
            transform={responsive('translateY(100%)', 'none')}
          >
            <ArrowNext mr="1em" lineWidth={responsive('2.5em', '5em')} color="lightGreen" width={responsive('1.5em', '3em')} />
            <Box.Inline p="0.5em" bg="linear-gradient(90deg, #B3D476 75%, transparent 100%)" borderRadius="50%">
              <ArrowIcon onClick={onNext} width={responsive('1.5em', '2em')} height={responsive('1.5em', '2em')} color="white" />
            </Box.Inline>
          </Flex>
        </Box>
      </BeforeStart>
    </CircleBgModule>
  )
}

export default WeatherThree
