import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Container from '../../../components/Container'
import Flex from '../../../components/Flex'
import Image from '../../../components/Image'
import Button from '../../../components/Button'

import chart_1 from './4-1-1.svg'
import chart_2 from './4-1-2.svg'
import CircleBgModule from '../../../components/CircleBgModule'
import { responsive } from '../../../components/ThemeProvider/theme'

const texts = [
  {
    title: '顛覆性創新者',
    text: '市場通常慢半拍才意識到出現了真正的顛覆式創新',
    src: chart_1
  },
  {
    title: '被顛覆的既有企業',
    text: '市場通常不願意接受顛覆式創新帶來的衝擊',
    src: chart_2
  },
]

const ChangeTwo = () => {
  return (
    <CircleBgModule bg="white" extend={responsive('-100%', 0)}>
      <Box position="relative" height={responsive('17.5vh', '30vh')}>
        <Box.AbsCenter top={responsive('67.5%', '50%')} width={responsive('80%', 'auto')} color="white">
          <Text.Bold fontSize={responsive('1.125em', '2.25em')}>顛覆性創新者未來將伴隨著顛覆性成長</Text.Bold>
          <Text fontSize={responsive('0.875em', '1.5em')}>施羅德認為，顛覆式創新能夠撼動行之有年的商業模式</Text>
        </Box.AbsCenter>
      </Box>
      <Box bg="linear-gradient(#82CAD0 0%, #FFFFFF 66%)">
        <Container height={responsive('82.5vh', '70vh')}>
          <Flex flexDirection={responsive('column', 'row')} my="1em" justifyContent="center">
            {texts.map(({ title, text, src }, i) => (
              <Box width={responsive('100%', '42.5%')} px="1em" key={i}>
                <Flex
                  flexDirection={responsive('row', 'column')}
                  alignItems={responsive('center', 'flex-start')}
                >
                  <Box.Inline
                    fontSize={responsive('0.75em', '1.25em')}
                    borderRadius="1em"
                    color="white"
                    px="1em"
                    py="0.25em"
                    bg="linear-gradient(90deg, #82CAD0 0%, #053D72 60%)"
                    mr={responsive('0.875em', 0)}
                  >{title}</Box.Inline>
                  <Text
                    mt={responsive('0', '0.5em')}
                    fontSize={responsive('0.5em', '1.125em')}
                    color="prussianBlue"
                  >{text}</Text>
                </Flex>
                <Box width={responsive('75%', null, '80%', null, '100%')} mx="auto">
                  <Image src={src} />
                </Box>
              </Box>
            ))}
          </Flex>
          <Box pt="0.5em" borderTop="1px solid">
            資料來源: 施羅德投資。相關圖表及所提及之公司或投資標的僅作為舉例說明，不代表任何金融商品之推介或投資建議。
          </Box>
        </Container>
      </Box>
    </CircleBgModule>
  )
}

export default ChangeTwo
