import React, { useState } from 'react'
import { QuestionIcon } from '@chakra-ui/icons'
import Box from './Box'
import Flex from './Flex'
import Text from './Text'
import Image from './Image'
import Button from './Button'
import ExplainBoard from './ExplainBoard'
import { responsive } from './ThemeProvider/theme'
import Container from './Container'
import useResponsive from '../contexts/mediaQuery/useResponsive'
import { IconButton } from '@chakra-ui/react'

const ContentModule = ({
    src,
    text,
    justifyContent,
    source,
    buttonText,
    buttonBg,
    textTitle,
    chartTitle,
    background,
    sourceColor,
    children,
    url,
    info,
    ...props
  }) => {
  const { isMobile } = useResponsive()
  const [openExplain, setExplain] = useState()
  const readMore = (
    <Button.SingleSharp
      href={url}
      mt={responsive(0, '1em')}
      bg={buttonBg}
      className="link-track"
      onClick={() => {
        window.gaTrackClick(`Link: ${url}`)
      }}
    >{buttonText}</Button.SingleSharp>
  )
  return (
    <Box background={background} {...props}>
      {children}
      <Container py={responsive('1.75em', null, '1.25em', null, '1.75em')}>
        <Flex alignItems="center" justifyContent={justifyContent || "flex-end"} flexDirection={responsive('column', 'row')}>
          <Box px={responsive('2em', 0)} width={responsive('100%', 35 / 100)} whiteSpace="pre-wrap" color="prussianBlue" mr={responsive(0, '2.75em')}>
            {textTitle && <Text.Bold pb="1em" fontSize={responsive('1em', '1.625em')}>{textTitle}</Text.Bold>}
            <Text fontSize={responsive('0.875em', '1em', '1.25em', null, '1.5em')}>{text}</Text>
            {buttonText && !isMobile && readMore}
          </Box>
          <Box mt={responsive('1em', 0)} width={responsive('100%', 1 / 2)}>
            <Flex alignItems="center">
              <Text color="brightBlue" fontWeight="700" fontSize={responsive('1em', '1.25em')}>{chartTitle}</Text>
              {info && (
                <IconButton
                  isRound
                  fontSize={responsive('1em', '1.25em')}
                  onClick={() => setExplain(!openExplain)}
                  icon={<QuestionIcon />}
                />
              )}
            </Flex>
            {typeof src === 'function' ? src() : <Image src={src} />}
            {source &&
              <Flex justifyContent="space-between" pt="0.5em" borderTop="1px solid" color={sourceColor} fontSize={responsive('0.75em', '0.875em')}>
                資料來源:{source}
                {buttonText && isMobile && readMore}
              </Flex>
            }
          </Box>
        </Flex>
      </Container>
      {openExplain && (
        <ExplainBoard
          openExplain={openExplain}
          exTitle="什麼是利差？"
          exText="債券利差是債券利率與公債利率間的差距， 是投資債券最重要的觀察指標之一，利差越小，價格越高，反之亦然。"
          setExplain={setExplain}
          top="30%"
        />
      )}
    </Box>
  )
}

export default ContentModule
