import React, { createElement, useEffect, useRef, useState } from 'react'
import { useTransition, animated } from 'react-spring'
import { IconButton } from '@chakra-ui/react'
import { get } from 'lodash'

import Flex from '../../../components/Flex'
import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Image from '../../../components/Image'
import Circle from '../../../components/Circle'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import BackgroundImage from '../../../components/BackgroundImage'
import ArrowIcon from '../../../components/ArrowIcon'
import LensIcon from '../../../components/LensIcon'
import ArrowNext from '../../../components/ArrowNext'
import PointingRight from '../../../components/PointingRight'
import { responsive } from '../../../components/ThemeProvider/theme'

import background from '../PredictTwo/background.png'
import rings from './rings.png'
import data from './data'
import Icons from './Icons'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'

import names from '../names'

const AnimatedBox = animated(Box)

// const bc = {
//   enter: 'green',
//   update: 'blue',
//   leave: 'red',
// }

const upper = ['25%', '45%']
const lower = ['75%', '55%']
const sunSize = responsive('88%', '80%', '66%', '55%')

const Page = ({
  item,
  props,
  directionInteroolate,
  pageDirection,
  slideDirection,
  onNext,
  onPrev,
  slideIndex,
  duration,
  isLast,
}) => {
  const slideTransition = useTransition(get(item.slides, slideIndex, item.slides[0]), slideIndex, {
    from: { x: slideDirection ? 100 * (slideDirection === 'right' ? 1 : -1) : 0 },
    enter: { x: 0 },
    leave: { x: slideDirection ? -100 * (slideDirection === 'right' ? 1 : -1) : 0 },
    config: { duration: duration * 0.75 },
  })
  const isLastSlide = slideIndex === item.slides.length - 1
  const { isMobile } = useResponsive()
  const readMore = (
    <Button.SingleSharp
      fontSize={responsive('0.75em', '1em')}
      href="https://www.schroders.com/zh-tw/tw/asset-management/insights/tic2020/event2-1120/"
      mt="1em"
      backgroundImage="linear-gradient(270deg, rgba(255, 169, 0, 0.7) -21.1%, rgba(248, 166, 3, 0.704) -19.56%, rgba(191, 137, 25, 0.7392) -5.92%, rgba(140, 111, 44, 0.7754) 8.09%, rgba(97, 90, 61, 0.8118) 22.15%, rgba(62, 72, 74, 0.8484) 36.29%, rgba(35, 58, 85, 0.8852) 50.56%, rgba(15, 49, 92, 0.9225) 64.97%, rgba(4, 43, 97, 0.9604) 79.64%, #002962 94.94%)"
      pointerEvents="all"
      className="link-track"
      onClick={() => {
        window.gaTrackClick(`Link: https://www.schroders.com/zh-tw/tw/asset-management/insights/tic2020/event2-1120/`)
      }}
    >完整論壇回顧</Button.SingleSharp>
  )
  return slideTransition.map((st) => (
    <Box.FullAbs key={st.key}>
      <Box height={responsive(...upper)} overflow="hidden">
        <Container position="relative" height="100%">
          {pageDirection === 'down' && (
            <AnimatedBox
              position="absolute"
              left="50%"
              top="100%"
              width={sunSize}
              style={{
                transform: props.scale.interpolate((scale => `translate(-50%, -45%) scale(${scale * 3})`)),
                // opacity: props.opacity,
              }}
            >
              <Circle bg="primary" />
            </AnimatedBox>
          )}
          {st.state !== 'enter' && (
            <animated.div
              style={{
                position: 'absolute',
                width: '120%',
                left: '50%',
                top: '100%',
                transform: props.scale.interpolate(directionInteroolate(scale => `translate(-50%, -45%) rotate(${(scale - item.index) * 30}deg)`)),
                opacity: props.opacity,
              }}
            >
              <Box transform={`rotate(${slideIndex * 30}deg)`} transition={`transform ${duration}ms ease-in-out`}>
                <Image src={rings} opacity="0.55" />
              </Box>
            </animated.div>
          )}
          {/* <Box position="absolute" opacity="0.2" top="0" bottom="0" left="0" right="0">
            <BackgroundImage src={background} />
          </Box> */}
          <AnimatedBox
            position="absolute"
            left="50%"
            top="100%"
            width={sunSize}
            style={{
              transform: props.scale.interpolate(directionInteroolate(scale => `translate(-50%, -45%) scale(${Math.max(0, 1 + (scale - 1) * 2)})`)),
              // opacity: props.opacity,
            }}
          >
            <Circle bg={st.state === 'enter' ? 'transparent' : 'sun'}>
              <AnimatedBox
                top="0"
                left="0"
                right="0"
                position="absolute"
                style={{
                  transform: st.props.x.interpolate(x => `translate3d(${x}%,0,0)`),
                }}
              >
                <Box color="primary" pt={responsive('4%', '12%')}>
                  {st.item.icon ? (
                    <Box width={responsive('2.5em', '3em')} mx="auto" mb={responsive('0.25em', '0.5em')} mt={responsive(0, '-2em')}>
                      <Icons name={st.item.icon} />
                    </Box>
                  ) : <Box height="1em" />}
                  <Flex alignItems="center" pb={responsive('0.25em', '1em')} mx={responsive('22%', '22.5%')}>
                    <Box borderBottom="4px solid">
                      <Text fontSize={responsive('2em', '5em')} lineHeight="1.125" fontWeight="bold">{st.item.no}</Text>
                    </Box>
                    <Box pl={responsive('1em', '2em')} pt="0.5em">
                      <Text
                        fontSize={responsive('1.125em', '2em')}
                        whiteSpace="pre-wrap"
                        fontWeight="700"
                      >{st.item.title}</Text>
                    </Box>
                  </Flex>
                  <Box pb="1em" textAlign="center" px="10%">
                    {st.item.subtitle && (
                      <Text color="white" fontSize={responsive('0.875em', '1.125em')}>{st.item.subtitle}</Text>
                    )}
                  </Box>
                </Box>
              </AnimatedBox>
            </Circle>
          </AnimatedBox>
          {pageDirection === 'up' && (
            <animated.div
              style={{
                position: 'absolute',
                width: '55%',
                left: '50%',
                top: '100%',
                transform: props.scale.interpolate((scale => `translate(-50%, -40%) scale(${3 - scale * 3})`)),
                // opacity: props.opacity,
              }}
            >
              <Circle bg="primary" />
            </animated.div>
          )}
        </Container>
      </Box>
      <Box position="relative" height={responsive(...lower)} overflow="hidden">
        <animated.div
          className="container"
          style={{
            opacity: props.contentOpacity.interpolate([0.5, 1], [0, 1]),
            transform: st.props.x.interpolate(x => `translate3d(${x}%,0,0)`),
          }}
        >
          <Flex color="primary" flexDirection={responsive('column', 'row')} px={responsive('1em', 0)}>
            <Box
              width={responsive('100%', '50%')}
              pr={responsive('1em', '2em')}
              pl={responsive('1em', '4em')}
              pt={responsive(item.slides.length > 1 ? '4em' : '2em', '2em')}
            >
              <Text textAlign="justify" whiteSpace="pre-wrap" fontSize={responsive('0.875em', '1em', '1.125em')}>{st.item.content}</Text>
              {isLast && !isMobile && readMore}
            </Box>
            <Box width={responsive('100%', '50%')} px="1em" pt={responsive('1em', '2em')}>
              <Text fontWeight="500" fontSize={responsive('0.875em', null, '1em', null, '1.25em')}>{st.item.chartTitle}</Text>
              {typeof st.item.chart === 'function' ? <Box px={responsive(0, null, '5%', null, 0)}>{createElement(st.item.chart)}</Box> : <Box px={responsive(0, null, '7.5%', null, 0)}><Image src={st.item.chart} /></Box>}
              {st.item.chartNote && (<Text fontSize={responsive('0.75em', '0.875em')} color="monza">{st.item.chartNote}</Text>)}
              <Text color="black" fontSize={responsive('0.75em', '0.875em')}>{st.item.chartSource}</Text>
              {isLast && isMobile && readMore}
            </Box>
          </Flex>
        </animated.div>
      </Box>
      {item.slides.length > 1 && (
        <AnimatedBox
          position="absolute"
          top={responsive('28%', '45%')}
          right="2em"
          left={responsive('2em', 'auto')}
          transform={responsive('', 'translate3d(0,-100%,0)')}
          style={{
            opacity: props.contentOpacity,
          }}
        >
          <Box pb="0.5em" textAlign="right">
            {slideIndex > 0 && (
              <Box position={responsive('absolute', 'static')} left="0">
                <IconButton
                  mb="1em"
                  bg="#7688A3"
                  color="sun"
                  width={responsive('3em', '4em')}
                  height={responsive('3em', '4em')}
                  rounded="full"
                  onClick={onPrev}
                  pointerEvents="all"
                  icon={<ArrowIcon fontSize={responsive('1.75em', '2.75em')} transform="rotate(180deg)" />}
                />
              </Box>
            )}
            {!isLastSlide && (
              <Flex ml="auto" width={responsive('6em', '10em')} alignItems="center">
                <PointingRight flex="1" pr="1em" mt={responsive('-0.25em', 0)}>
                  <ArrowNext lineWidth={responsive('3em', '5em')} width="100%" color={responsive('primary', 'sun')} />
                </PointingRight>
                <IconButton
                  pointerEvents="all"
                  height={responsive('3em', '4em')}
                  rounded="full"
                  bg="transparent"
                  backgroundImage={responsive('linear-gradient(270deg, rgba(255, 169, 0, 0.7) -21.1%, rgba(248, 166, 3, 0.704) -19.56%, rgba(191, 137, 25, 0.7392) -5.92%, rgba(140, 111, 44, 0.7754) 8.09%, rgba(97, 90, 61, 0.8118) 22.15%, rgba(62, 72, 74, 0.8484) 36.29%, rgba(35, 58, 85, 0.8852) 50.56%, rgba(15, 49, 92, 0.9225) 64.97%, rgba(4, 43, 97, 0.9604) 79.64%, #002962 94.94%)', 'linear-gradient(90deg, rgba(248, 166, 3, 0.87) 61.51%, rgba(0, 41, 98, 0.94) 118.16%)')}
                  width={responsive('3em', '4em')}
                  color="white"
                  onClick={onNext}
                  icon={<LensIcon fontSize={responsive('1.75em', '2.75em')} />}
                  _after={{
                    content: `"${st.item.no + 1}"`,
                    position: 'absolute',
                    left: '50%',
                    top: '50%',
                    transform: 'translate(-50%,-50%)',
                    fontSize: '1.5em',
                  }}
                ></IconButton>
              </Flex>
            )}
          </Box>
        </AnimatedBox>
      )}
    </Box.FullAbs>
  ))
}

const Pages = ({
  index,
  slideIndex = 0,
  duration,
  isLast,
  pageDirection,
  slideDirection,
  onNext,
  onPrev,
}) => {
  const pages = useTransition(data[index] || {}, d => d.index, {
    from: { scale: 0, opacity: 0, contentOpacity: 0 },
    enter: { scale: 1, opacity: 1, contentOpacity: 1 },
    leave: { scale: 2, opacity: 1, contentOpacity: 0 },
    config: { duration },
  })
  const directionInteroolate = (f => scale => f(1 + (pageDirection === 'down' ? scale - 1 : 1 - scale)))
  return (
    <Box.FullAbs>
      <Box height={responsive(...upper)} overflow="hidden"></Box>
      <Box height={responsive(...lower)} backgroundImage={`linear-gradient(180deg, #FFA900 -18.04%, #FFAB07 -16.51%, #FFBF40 -2.95%, #FFD073 10.99%, #FFDE9E 24.98%, #FFEAC1 39.05%, #FFF3DC 53.23%, #FFFAF0 67.57%, #FFFEFB 82.16%, #FFFFFF 97.38%)`}></Box>
      {pages.map(({ item, key, props, state }) => {
        return (
          <Box.FullAbs key={key}>
            <Page
              item={item}
              props={props}
              pageDirection={pageDirection}
              slideDirection={slideDirection}
              directionInteroolate={directionInteroolate}
              slideIndex={slideIndex}
              onNext={onNext}
              onPrev={onPrev}
              state={state}
              duration={duration}
              isLast={isLast}
            />
          </Box.FullAbs>
        )
      })}
    </Box.FullAbs>
  )
}

export default Pages
