import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import ArrowNext from '../../../components/ArrowNext'
import ArrowIcon from '../../../components/ArrowIcon'
import CircleBgModule from '../../../components/CircleBgModule'
import ContentModule from '../../../components/ContentModule'
import { responsive } from '../../../components/ThemeProvider/theme'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'


const ArrowButton = ({
    index,
    onPrev,
    onNext,
    isLast,
    arrowPrevIconColor,
    arrowNextColor,
    arrowNextIconBg,
    isMobile
  }) => (
  <>
    {Boolean(index) && (
      <Box.Inline transform="rotate(180deg)" p="0.5em" bg="bgBlue" borderRadius="50%">
        <ArrowIcon onClick={onPrev} width="2em" height="2em" color={arrowPrevIconColor} />
      </Box.Inline>
    )}
    {!isLast && (
      <Flex alignItems="center" mt={responsive(0, '1em')}>
        <ArrowNext mr={responsive('0.5em', '1em')} lineWidth={responsive('3em', '5em')} color={arrowNextColor} width={isMobile ? '2em' : '3em'} />
        <Box.Inline p="0.5em" background={arrowNextIconBg} borderRadius="50%">
          <ArrowIcon onClick={onNext} width="2em" height="2em" color="white" />
        </Box.Inline>
      </Flex>
    )}
  </>
)

const WeatherThreeThree = ({
  onPrev,
  onNext,
  title,
  textTitle,
  text,
  src,
  source,
  isLast,
  index,
  subTitle,
  chartTitle,
  background,
  buttonBg,
  arrowPrevIconColor,
  arrowNextColor,
  arrowNextIconBg,
  info,
  url
}) => {
  const { isMobile } = useResponsive()
  return (
    <CircleBgModule extend={responsive("-100%", 0)}>
      <Flex flexDirection="column" height="100vh">
        <Box position="relative" height={responsive('25vh', '40vh')}>
          <Box
            position="absolute"
            width={responsive('75%', 'auto')}
            bottom={responsive('50%', '12.75%')}
            left="50%"
            transform={responsive('translate(-50%, 50%)', 'translateX(-50%)')}
          >
            <Text.Bold color="white" whiteSpace="pre-wrap" letterSpacing="0.25em" fontSize={responsive('1.125em', '2.25em')}>
              {title}
            </Text.Bold>
            <Text letterSpacing="0.25em" color="white" fontSize={responsive('0.875em', '1.5em')}>{subTitle}</Text>
          </Box>
          {!isMobile && (
            <Box position="absolute" textAlign="right" right="2em" bottom="1em">
              <ArrowButton
                index={index}
                onPrev={onPrev}
                onNext={onNext}
                isLast={isLast}
                arrowPrevIconColor={arrowPrevIconColor}
                arrowNextColor={arrowNextColor}
                isMobile={isMobile}
                arrowNextIconBg={arrowNextIconBg}
              />
            </Box>
          )}
        </Box>
        <ContentModule
          textTitle={textTitle}
          chartTitle={chartTitle}
          text={text}
          background={background}
          buttonBg={buttonBg}
          buttonText="完整論壇回顧"
          flex={1}
          src={src}
          source={source}
          url={url}
          info={info}
        >
          {isMobile && (
            <Flex justifyContent={Boolean(index) ? 'space-between' : 'flex-end'} pt="1em" px="1em">
              <ArrowButton
                index={index}
                onPrev={onPrev}
                onNext={onNext}
                isLast={isLast}
                arrowPrevIconColor={arrowPrevIconColor}
                arrowNextColor={arrowNextColor}
                isMobile={isMobile}
                arrowNextIconBg={arrowNextIconBg}
              />
            </Flex>
          )}
        </ContentModule>
      </Flex>
    </CircleBgModule>
  )
}

export default WeatherThreeThree
