import React from 'react'

// import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import ContentModule from '../../../components/ContentModule'
import Title from '../../../components/Title'

import Chart from './Chart22'

const WeatherOne = () => {
  return (
    <Flex flexDirection="column" height="100vh">
      <Title height="auto" titleWidth="60%" subTitle="現在是投資永續能源的絕佳時機" text="在全球三分之二地區，風電與太陽能已成為最便宜的能源來源" />
      <ContentModule
        text={"全球再生能源成本持續下降，有三分之二地區的風電和太陽能已成為最便宜的能源來源。加上儲能的成本亦下滑，促使再生能源邁入「近全時供電」（near firm）的新階段。\n\n「1900年代能源從生質燃料轉向煤炭，1960年代則從煤炭轉向石油和天然氣，目前能源系統的結構性轉變才剛開始，正是投資能源轉型的絕佳時機。」施羅德投資（環）環球氣候變化策略基金經理人Simon Webber表示。"}
        source="BNEF, IRENA, 施羅德投資，截至 2020/6/30"
        sourceColor="black"
        title="全球再生能源歷史均化成本"
        src={Chart}
        background="linear-gradient(#A9D668 0%, #FFFFFF 66%)"
        flex={1}
      />
    </Flex>
  )
}

export default WeatherOne
