import React, { useEffect, useState } from 'react'
import styled from '@emotion/styled'
import { keyframes } from '@emotion/react'

import Box from '../../components/Box'
import injectProps from '../../components/utils/injectProps'
import { isArray } from 'lodash'

const fromRotate = keyframes`
from {
  transform: rotate(0deg);
}
to {
  transform: rotate(-90deg);
}
`

const toRotate = keyframes`
from {
  transform: rotate(90deg);
}
to {
  transform: rotate(0deg);
}
`
const StyledFrom = styled(Box.FullAbs)`
  transform-origin: 50% 100%;
  animation: ${fromRotate};
  ${injectProps('animationDuration')}
`

const StyledTo = styled(Box.FullAbs)`
  transform-origin: 50% 100%;
  animation: ${toRotate};
  ${injectProps('animationDuration')}
`

const PageRotate = ({ active, from, to, duration, onFinish, currentSlide }) => {
  useEffect(() => {
    if (active) {
      setTimeout(() => {
        onFinish()
      }, duration)
    }
  }, [active, duration])
  return active ? (
    <Box.FullAbs height="150vh" bg="primary">
      <StyledFrom animationDuration={`${duration}ms`}>
        <Box.Relative height="100vh">{isArray(from) ? from[currentSlide] : from}</Box.Relative>
      </StyledFrom>
      <StyledTo animationDuration={`${duration}ms`}>
      <Box.Relative height="100vh">{isArray(to) ? to[currentSlide] : to}</Box.Relative>
      </StyledTo>
    </Box.FullAbs>
  ) : null
}

export default PageRotate
