import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react'
import ReactFullpage from '@fullpage/react-fullpage';
import { Waypoint } from 'react-waypoint';
import { get } from 'lodash';
import 'fullpage.js/vendors/scrolloverflow'

import Container from '../../components/Container'
import Box from '../../components/Box'
import Image from '../../components/Image'
import Button from '../../components/Button'

import useResponsive from '../../contexts/mediaQuery/useResponsive'

import PredictStart from './PredictStart';
import WeatherStart from './WeatherStart';
import OpportunityStart from './OpportunityStart';
import ChangeStart from './ChangeStart';
import Predict from './PredictOne';
import forecastData from './TenForecast/data';
import TenForecast from './TenForecast';
import WePredict from './PredictTwo';
import WeatherOne from './WeatherOne';
import WeatherTwo from './WeatherTwo';
import WeatherThree from './WeatherThree';
import weatherThreeData from './BranchModule/weatherData';
import optData from './BranchModule/optData';
import BranchModule from './BranchModule';

import OptOne from './OptOne';
import OptTwo from './OptTwo';
import ChangeOne from './ChangeOne';
import ChangeTwo from './ChangeTwo';

import banner from './banner.png'
import underBanner from './under-banner.png'
import TheMap from './TheMap/Loadable'
import SideNav from './CircleNav';
import PageRotate from './PageRotate';
import ChangeFields from './ChangeFields';
import FpContext from './fpContext'
import ArrowDown from '../../components/ArrowDown';

import links from './links'
import { responsive } from '../../components/ThemeProvider/theme';

let fpApi
let rotateTransition
let header
let contact
let footer
let lastWheelDeltaY
let wheelTick
let clearWheelDeltaY
let lastTouchOffsetY
let lastTouchDeltaY
let touchTick
let clearTouchDeltaY
const scrollingSpeed = 1000
const forecastStart = 4
const parts = [
  {
    name: '十大投資預測',
    color: 'sun',
    children: [
      <PredictStart onDown={() => fpApi.moveSectionDown()} />,
      <Predict />,
      <WePredict getFpApi={() => fpApi} />,
      ...forecastData.map((d, i) => d.slides.length > 1 ? d.slides.map((dd, j) => <div className="slide" key={`${i}${j}`} />) : <div key={i} />),
    ],
  },
  {
    name: '氣候投資機會',
    color: 'lightGreen',
    children: [
      <WeatherStart onDown={() => fpApi.moveSectionDown()} />,
      <WeatherOne />,
      <WeatherTwo />,
      [
        <WeatherThree
          onNext={() => fpApi.moveSlideRight()}
          onPrev={() => fpApi.moveSlideLeft()}
        />,
        ...weatherThreeData.map(({ title, textTitle, text, src, source }, k) => (
          <BranchModule
            title={title}
            textTitle={textTitle}
            text={text}
            src={src}
            source={source}
            onNext={() => fpApi.moveSlideRight()}
            onPrev={() => fpApi.moveSlideLeft()}
            url={links[1]}
            index={k}
            isLast={k === weatherThreeData.length - 1}
            key={k}
            background="linear-gradient(#A9D668 0%, #FFFFFF 66%)"
            buttonBg="linear-gradient(90deg, #002962 0%, #B3D476 100%)"
            arrowPrevIconColor="lightGreen"
            arrowNextColor={responsive('prussianBlue', 'lightGreen')}
            arrowNextIconBg="linear-gradient(90deg, #002962 10%, #B3D476 50%)"
          />
        )),
      ].map((comp, i) => <div key={i} className="slide" style={{height: '100%' }}>{comp}</div>),
    ],
  },
  {
    name: '債信投資機會',
    color: 'lightPurple',
    children: [
      <OpportunityStart onDown={() => fpApi.moveSectionDown()} />,
      <OptOne />,
      <OptTwo />,
      [
        ...optData.map(({ title, subTitle, text, src, source, chartTitle, info }, k) => (
          <BranchModule
            title={title}
            subTitle={subTitle}
            text={text}
            src={src}
            source={source}
            chartTitle={chartTitle}
            onNext={() => fpApi.moveSlideRight()}
            onPrev={() => fpApi.moveSlideLeft()}
            url={links[2]}
            index={k}
            info={info}
            isLast={k === optData.length - 1}
            key={k}
            background="linear-gradient(#CABFEC 0%, #FFFFFF 66%)"
            buttonBg="linear-gradient(90deg, #002962 20%, #CABFEC 90%)"
            arrowPrevIconColor="lightPurple"
            arrowNextColor={responsive('prussianBlue', 'lightPurple')}
            arrowNextIconBg="linear-gradient(90deg, #002962 0%, #CABFEC 100%)"
          />
        )),
      ].map((comp, i) => <div key={i} className="slide" style={{height: '100%' }}>{comp}</div>),
    ],
  },
  {
    name: '最強顛覆產業',
    color: 'lakeBlue',
    children: [
      <ChangeStart onDown={() => fpApi.moveSectionDown()} />,
      <ChangeOne />,
      <ChangeTwo />,
      <ChangeFields />,
    ],
  },
]

let fpModeMark
const partNav = parts.reduce((nav, part, i) => [...nav, part.children.length + nav[i]], [1])
const partMark = parts.reduce((mark, part, i) => mark.concat(part.children.map(() => i)), [])
const partTrack = parts.reduce((mark, part, i) => mark.concat(part.children.map((_, j) => [i, j])), [])
const partColors = partMark.map(mark => parts[mark].color)

const HomePage = ({ done }) => {
  const { isMobile } = useResponsive()
  const [fullpageMode, setFullpageMode] = useState()
  const [prevPage, setPrevPage] = useState(0)
  const [currentPage, setCurrentPage] = useState(0)
  const [currentSlide, setCurrentSlide] = useState(0)
  const [pageDirection, setPageDurection] = useState()
  const [slideDirection, setSlideDurection] = useState()
  const [isRotate, setIsRotate] = useState()
  const lastPage = useRef()
  // console.log(currentPage)
  useEffect(() => {
    header = document.querySelector(".header")
    contact = document.querySelector(".contact")
    footer = document.querySelector(".footer")
    window.onscroll = e => {
      if (fpModeMark) {
        e.preventDefault()
        window.scroll(0, 0)
        setTimeout(() => {
          window.onscroll = null
        }, 1000)
      }
    }
  }, [])
  useEffect(() => {
    if (fullpageMode) {
      window.addEventListener('wheel', e => {
        if (!wheelTick) {
          if (fpApi.getActiveSection().isLast && e.deltaY < 0) {
            fpApi.moveSectionUp()
          }
          if (lastWheelDeltaY < 0 && e.deltaY < 0) {
            header.style.transform = 'translateY(0)'
          } else {
            header.style.transform = 'translateY(-100%)'
          }
          lastWheelDeltaY = e.deltaY
          wheelTick = true
        }
        setTimeout(() => {
          wheelTick = false
        }, 500)
        if (clearWheelDeltaY) {
          clearTimeout(clearWheelDeltaY)
        }
        clearWheelDeltaY = setTimeout(() => {
          lastWheelDeltaY = null
        }, 1000)
      })
      window.addEventListener('touchmove', e => {
        if (!touchTick) {
          const touchDeltaY = e.touches[0].clientY - lastTouchOffsetY
          if (fpApi.getActiveSection().isLast && touchDeltaY < 0) {
            fpApi.moveSectionUp()
          }
          if (lastTouchDeltaY < 0 && touchDeltaY < 0) {
            header.style.transform = 'translateY(0)'
          } else {
            header.style.transform = 'translateY(-100%)'
          }
          lastTouchOffsetY = e.touches[0].clientY
          lastTouchDeltaY = touchDeltaY
          touchTick = true
        }
        setTimeout(() => {
          touchTick = false
        }, 500)
        if (clearTouchDeltaY) {
          clearTimeout(clearTouchDeltaY)
        }
        clearTouchDeltaY = setTimeout(() => {
          lastTouchDeltaY = null
        }, 1000)
      })

      lastPage.current.appendChild(contact)
      lastPage.current.appendChild(footer)
    }
  }, [fullpageMode])

  const enterFullpageMode = useCallback(() => {
    fpModeMark = true
    header.style.position = 'fixed'
    header.style.left = 0
    header.style.right = 0
    header.style.zIndex = 1
    header.style.transition = 'transform 750ms ease'
    header.style.transform = 'translateY(-100%)'
    setTimeout(() => setFullpageMode(true), 200)
  }, [])

  const pages = [
    (
      <Box bg="primary" color="white" overflow="hidden" height="100%">
        <Container height="100%">
          <Box.Relative mx="-45%" height="100%">
            <Box.Absolute
              left="0"
              right="0"
              height="100%"
              backgroundImage={`url(${underBanner})`}
              backgroundPosition="50% 0"
              backgroundSize="100%"
              backgroundRepeat="no-repeat"
            />
            <Box display="table" width="100%" height="100%" style={{ tableLayout: 'fixed' }}>
              <TheMap
                display="table-cell"
                height="100%"
                style={{ verticalAlign: 'middle' }}
                onClick={(id) => {
                  if (fpApi) {
                    fpApi.moveTo(partNav[id] + 1, 0)
                  } else {
                    enterFullpageMode()
                    setTimeout(() => {
                      setTimeout(() => {
                        fpApi.moveTo(partNav[id] + 1, 0)
                      })
                    }, 1000)
                  }
                }}
              />
            </Box>
          </Box.Relative>
        </Container>
      </Box>
    ),
    ...parts.reduce((partPages, part) => partPages.concat(part.children), []),
    <Box height="100%" overflowY="scroll" overflowX="hidden" ref={lastPage} className="normal-scroll" />,
  ]
  const fpContent = useMemo(() => (
    <ReactFullpage
      //fullpage options
      normalScrollElements=".normal-scroll"
      controlArrows={false}
      licenseKey={process.env.REACT_APP_FULLPAGE_JS_KEY}
      scrollingSpeed={scrollingSpeed} /* Options here */
      keyboardScrolling={false}
      // scrollOverflow
      afterRender={() => {
        setTimeout(() => {
          fpApi.setAllowScrolling(false)
          setTimeout(() => {
            window.scroll(0, 0)
            fpApi.setAllowScrolling(true)
          }, 500)
        })
      }}
      onLeave={(origin, destination, direction) => {
        setCurrentPage(destination.index)
        setPrevPage(origin.index)
        setPageDurection(direction)
        setSlideDurection(null)
        const dest = partTrack[destination.index - 1]
        const toTrack = dest && dest.map(d => d + 1)
        if (partMark[origin.index - 1] !== partMark[destination.index - 1]) {
          if (rotateTransition) {
            if (toTrack) window.gaTrackPageView(...toTrack)
            rotateTransition = false
          } else {
            setIsRotate(true)
            return false
          }
        } else {
          if (toTrack) window.gaTrackPageView(...toTrack)
        }
      }}
      onSlideLeave={(section, origin, destination, direction) => {
        setCurrentSlide(destination.index)
        setSlideDurection(direction)
        setPageDurection(null)
        const toTrack = partTrack[section.index - 1].map(d => d + 1)
        window.gaTrackPageView(...toTrack, destination.index + 1)
      }}
      render={({ fullpageApi }) => {
        fpApi = fullpageApi
        return (
          <ReactFullpage.Wrapper>
            {pages.map((page, i) => (
              <div className="section" key={i}>
                {page}
                {i && partNav.some(id => id - 1 === i) && parts[partMark[i]] && (
                  <Box.Absolute left="1em" bottom="2em" zIndex={1000} display={responsive('none', 'block')}>
                    <Button
                      leftIcon={<ArrowDown width="1.5em" />}
                      variant="ghost"
                      _hover={{
                        bg: 'transparent',
                      }}
                      _focus={{
                        bg: 'transparent',
                      }}
                      _active={{
                        bg: 'transparent',
                      }}
                      py="0.25em"
                      color={get(parts[partMark[i]], 'color')}
                      onClick={() => fpApi.moveSectionDown()}
                    >
                      {get(parts[partMark[i]], 'name')}
                    </Button>
                  </Box.Absolute>
                )}
              </div>
            ))}
          </ReactFullpage.Wrapper>
        );
      }}
    />
  ), [])
  // console.log('index currentPage', currentPage)
  return (
    <div>
      {fullpageMode ? (
        <Box bg="primary">
          <FpContext.Provider value={{ currentSlide, currentPage }}>
            {fpContent}
          </FpContext.Provider>
          <PageRotate
            active={isRotate}
            onFinish={() => {
              rotateTransition = true
              fpApi.silentMoveTo(currentPage + 1)
              setTimeout(() => {
                setIsRotate(false)
              })
            }}
            currentSlide={currentSlide}
            duration={scrollingSpeed}
            from={pages[prevPage]}
            to={pages[currentPage]}
          />
          <TenForecast
            index={Math.min(Math.max(currentPage - forecastStart, 0), forecastData.length - 1)}
            isLast={currentPage === forecastStart + forecastData.length - 1}
            slideIndex={currentSlide}
            visible={currentPage >= forecastStart && currentPage < forecastStart + forecastData.length}
            duration={scrollingSpeed}
            pageDirection={pageDirection}
            slideDirection={slideDirection}
            onNext={() => fpApi.moveSlideRight()}
            onPrev={() => fpApi.moveSlideLeft()}
          >
            <Box.Absolute left="1em" bottom="2em" display={responsive('none', 'block')}>
              <Button
                leftIcon={<ArrowDown width="1.5em" />}
                variant="ghost"
                _hover={{
                  bg: 'transparent',
                }}
                _focus={{
                  bg: 'transparent',
                }}
                _active={{
                  bg: 'transparent',
                }}
                py="0.25em"
                color={parts[1].color}
                onClick={() => fpApi.moveSectionDown()}
              >{parts[1].name}</Button>
            </Box.Absolute>
          </TenForecast>
          <SideNav
            active={currentPage && currentPage != 3}
            bg={partColors[currentPage - 1]}
            activePart={partMark[currentPage - 1]}
            onClick={(id) => fpApi.moveTo(partNav[id] + 1, 0)}
            colors={parts.map(d => d.color)}
          />
        </Box>
      ) : (
        <>
          <Box bg="#00609F" overflow="hidden">
            <Container>
              <Box mx="-6%">
                <Image src={banner} />
              </Box>
            </Container>
          </Box>
          {done && (
            <>
              <Box height="100vh">
                <Waypoint onLeave={enterFullpageMode} />
                {pages[0]}
              </Box>
              <Box height="100vh">
                {pages[1]}
              </Box>
            </>
          )}
        </>
      )}
    </div>

  )
}

export default HomePage
