import React from 'react'

import Box from '../Box'
import ExplainBoard from '../ExplainBoard'
import Flex from '../Flex'
import Text from '../Text'
import Image from '../Image'
import { responsive } from '../ThemeProvider/theme'

import left from './left.svg'
import right from './right.svg'

const Title = ({
    titleWidth,
    title,
    subTitle,
    subTitleSpacing,
    text,
    note,
    noBg,
    exTitle,
    exText,
    openExplain,
    setExplain,
    ...props
  }) => {
  // const [openExplain, setExplain] = useState(true)
  return (
    <Flex color="white" position="relative" bg="prussianBlue" {...props}>
      {!noBg &&
        <>
          <Box width="50%">
            <Image src={left} bg="prussianBlue" />
          </Box>
          <Box width="50%" >
            <Image src={right} bg="prussianBlue" />
          </Box>
        </>
      }
      <Box.AbsCenter width={titleWidth}>
        <Text fontSize={responsive('1.25em', '3em')}>{title}</Text>
        <Text letterSpacing={subTitleSpacing} whiteSpace="pre-wrap" fontSize={responsive('1em', '2em')}>{subTitle}</Text>
        <Text whiteSpace="pre-wrap" fontSize={responsive('0.875em', '1.375em')}>{text}</Text>
        {note && <Text pt="0.5em" color="red" fontSize="0.875em">*{note}</Text>}
      </Box.AbsCenter>
      {openExplain && (
        <ExplainBoard
          exTitle={exTitle}
          exText={exText}
          setExplain={setExplain}
          openExplain={openExplain}
        />
      )}
    </Flex>
  )
}

export default Title
