import React from 'react'

import Box from '../Box'
import Flex from '../Flex'

import { ReactComponent } from './arrow-next.svg'

const ArrowNext = ({ lineWidth, width, ...props }) => (
  <Flex display="inline-flex" alignItems="center" {...props}>
    <Box
      width={lineWidth}
      height="3px"
      bg="currentColor"
      mr="0.25em"
    />
    <ReactComponent style={{ width }} />
  </Flex>
)

ArrowNext.defaultProps = {
  width: '3em',
}

export default ArrowNext
